"use client";
import BackArrow from "@/app/components/ui/BackArrow";
import Button from "@/app/components/ui/Button";
import Input from "@/app/components/ui/Input";
import { useRouter } from "next/navigation";
import React from "react";

const Page = () => {
  const router = useRouter();

  return (
    <div className="py-20 mx-auto max-w-200 relative h-screen flex flex-col justify-between px-4">

      {/* Inputs */}
      <div className="flex flex-col gap-8 ">
        <BackArrow />
        <Input
          headline="نسيت كلمة المرور؟"
          placeholder="ادخل بريدك الاكتروني"
          isInput={false}
          isPassInput={false}
        />
        <Input
          headline="البريد الاكتروني"
          placeholder="anashisham@gmail.com"
          isInput={true}
          isPassInput={false}
        />
      </div>

      {/* Submit Button */}
      <div>
        <Button isMain text="ارسال" onClick={() => router.push("/")} />
      </div>
    </div>
  );
};

export default Page;
